body{
    background-image: linear-gradient(to top, #4ca1af, #c4e0e5);
}

.logo-icon{
    font-size: 2rem;
}

#menu{
    display: none;
}

label{
    cursor: pointer;
}

input:checked ~ #menu-content {
    display: block;
}

.navbar__brand{
    font-size: 2rem;
    font-weight: 600;
    letter-spacing: 5px;    
    padding: 0;
}

.navbar__nav{
    margin-left: auto !important;
}

.nav__item{
    margin-left: 0.5rem;
    padding: 0.5rem 0.75rem;
    margin: 0.5rem;
    background-color: tomato;
    color: whitesmoke;
    font-size: 1.1rem;
    font-weight: 400;
    cursor: pointer;
    border: none;
    border-radius: 3px;
    text-decoration: none;
}

.nav__item:hover {
    color: inherit;
    background-color: rgba(37, 99, 235, .2);
}

.link{
    text-decoration: none;
}

.link:hover{
    color: inherit;
}


.bulb-img{
    max-width: 400px;
    height: 550px;
    margin: auto;
    clip-path: circle(50%);
}

img{
    width: 100%;
    height: 100%;
}

.form-container{
    min-height: 600px;
    display: grid;
    place-items: center;
}


form{
    border-radius: 8px;
    background-color: whitesmoke;
    box-shadow:  0 10px 15px -3px rgba(0, 0, 0, 0.1), 0 4px 6px -2px rgba(0, 0, 0, 0.05);
}

.button{
    border:none;
    background-color: tomato;
}

.dash-container{
    min-height: 580px;
    background: linear-gradient(110deg, #141e30 30%, #243b55 30%);
}

.head{
    text-align: end;
}

.hidden{
    display: none;
}

#alert{
    text-align: center;
}

.url-form{
    width: 20rem;
    height: 20rem;
    display: grid;
    place-content: center;
}

.url-table{
    max-width: 600px;
    height: 400px;
    margin: 1rem;
    padding: 1.5rem;
    display: block;
    overflow: scroll;   
    background-color: whitesmoke;
    border-radius: 5px;
}

.footer{
    color: whitesmoke;
    background-color: tomato;
}


@media(max-width: 991px){
    .head{
        text-align: center;
    }
    .card__{
        margin: auto
    }
}